<template>
    <div class="container text-center pluginHome home-gallery">
        <h1 class="heading">v-selectmenu</h1>

        <p align="center">
            <iframe src="https://ghbtns.com/github-btn.html?user=TerryZ&repo=v-selectmenu&type=star&count=true" frameborder="0" scrolling="0" width="100px" height="20px"></iframe>
            <iframe src="https://ghbtns.com/github-btn.html?user=TerryZ&repo=v-selectmenu&type=fork&count=true" frameborder="0" scrolling="0" width="80px" height="20px"></iframe>
        </p>
        <p align="center">
            <v-gallery type="carousel" :images="list" ></v-gallery>
        </p>
        <p><i18n path="selectmenu.projectDesc"></i18n></p>

        <p>
            <a href="https://www.npmjs.com/package/v-selectmenu" target="_blank">
                <img src="https://img.shields.io/npm/v/v-selectmenu.svg" alt="v-selectmenu-npm">
            </a>
            <a href="https://mit-license.org/" target="_blank">
                <img src="https://img.shields.io/badge/license-MIT-brightgreen.svg" >
            </a>
        </p>
    </div>
</template>

<script>
    export default {
        data(){
            return {
                list: [
                    {
                        title: 'Advance menu mode group type',
                        url: `${this.$store.state.baseImgUrl}/image/v-selectmenu/v-selectmenu-advance.png`
                    },{
                        title: 'The regular menu',
                        url: `${this.$store.state.baseImgUrl}/image/v-selectmenu/v-selectmenu-regular.png`
                    },{
                        title: 'The regular menu group type',
                        url: `${this.$store.state.baseImgUrl}/image/v-selectmenu/v-selectmenu-regular-group.png`
                    }
                ]
            };
        }
    }
</script>


